const route=[

    {
      path: '/',
      name: 'ico',
      component: resolve => require(['@/views/ico/index'], resolve),
      meta: {
        title: 'ico',
        requireAuth: false,
        pageType: 'ico',
        appstyle:'default',
        headerShow:true,
        footerShow:true,
        keepalive:false
      }
    },

  ]

export { route }



