const i18n = new VueI18n({
  locale: "en-us",
  messages: {
    "en-us": require("./lang/en-us.js"),
    "zh-cn": require("./lang/zh-cn.js"),
    "zh-hk": require("./lang/zh-hk.js")
  }
});


//init web3 for window.web3
var Web3 = require("web3");
var essabi = require("@/abi/ess");
window.essAddress="";
if (typeof web3 !== 'undefined') {
    window.web3 = new Web3(web3.currentProvider);
    window.essAddress = "0x3817a4eedf0d27c773210ce07ce584a20c036584";
    //初始化相关的合约
    window.essContract = new web3.eth.Contract(essabi,essAddress);

     console.log(2);
    web3.eth.getAccounts(function(err,accounts) {
     console.log(3);
        if (err != null || accounts.length == 0) {
            // console.log("小狐狸还没有登录，锁住中");
            MessageBox.confirm(i18n.t("m.public.address_not_found"), i18n.t("m.public.tip"), {
                  confirmButtonText: i18n.t("m.public.confirm"),
                  showClose:false,
                  showCancelButton:false,
                  type: 'warning'
            }).then(() => {
                // 确定
              location.reload();
            }).catch(() => {    
              location.reload();   
            });
        }
        else {
     console.log(4);
            window.eth_account = accounts[0];
        }
     console.log(5);
        initVue();
    });
} else {
    // console.log("小狐狸没安装");
    MessageBox.confirm(i18n.t("m.public.dapp_not_support"), i18n.t("m.public.tip"), {
          confirmButtonText: i18n.t("m.public.confirm"),
          showClose:false,
          showCancelButton:false,
          type: 'warning'
    }).then(() => {
        // 确定
      location.reload();
    }).catch(() => {    
      location.reload();   
    });

    initVue();
}
//end init web3

function initVue(){
    /* eslint-disable no-new */
    new Vue({
        el: '#app',
        i18n,
        VueAwesomeSwiper,
        router,
        store,
        components: { App },
        template: '<App/>'
    });
}
window.checkweb3 = function(){
    if (typeof web3 !== 'undefined'&& typeof eth_account!='undefined'){
        return true;
    }
    else
    {
        return false;
    }
};



